.container-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  text-align: center;
  flex-direction: column;
  margin-top: 10%;
  margin-bottom: 3%;
  overflow: hidden;
}
.body-style {
  width: 90%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  color: black;
  overflow: hidden;
  grid-gap: 4%;
}
.sub-content {
  margin-top: 3%;
  padding: 10%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 2em;
  font-size: 140%;
  background-color: #ffbac2;
}

@media (max-width: 820px) {
  .sub-content {
    margin-top: 3%;
    padding: 10%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 2em;
    font-size: 80%;
  }
}

@media (max-width: 430px) {
  .sub-content {
    margin-top: 3%;
    padding: 10%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 2em;
    font-size: 50%;
  }
}

.review-img {
  width: 100%;
}
.opening-time {
  font-weight: 700;
  background: #ffbac2;
  color: black;
  margin: 4%;
  padding: 4%;
  border: 1px solid black;
  border-radius: 1.5rem;
}
.h3-style {
  color: black;
  margin-bottom: 0;
  margin-top: 15%;
}
